/**
 * Helper function to perform a linear interpolation between two colors..
 *
 * Function Arguments:
 * Color1 and Color2: two number arrays with R, G, B, A elements.
 * The RGB values have a 0-1 range.
 * 
 * Factor is the location inside the interpolation: From 0-1 (0 to 100%).
 * 
 * */

//Helper function to display gradients (through a color lerp)\
    // Interpolates two [r,g,b] colors and returns an [r,g,b] of the result
        // Taken from the ROT.js roguelike dev library at
            // https://github.com/ondras/rot.js. Source https://codepen.io/njmcode/pen/NWdYBy
    export function interpolateColor (color1, color2, factor) {
                if (arguments.length < 3) { factor = 0.5; }
                var result = color1.slice();
                for (var i=0;i<color1.length;i++) {
                    result[i] = Math.round(result[i] + factor*(color2[i]-color1[i]));
                }
                              
                return result;
                };


