//Delete Bullet Function.
        deleteBulletInList(bulletName, collidedWith= "")
        {
            //Do nothing if bullet is not in list.
            if(!(bulletName in this.firedBulletsList))
                {   
                    console.log("deleteBulletInList() bug: Bullet not in list.");
                    return;
                }

            const bulletToDelete= this.firedBulletsList[bulletName];


            //If the bullet is owned by Local Player, 
                //send a message to the Remote Player with a message to delete the remote Bullet.
        
            if (bulletToDelete.isOwnedByLocalPlayer)
                {

                    if(collidedWith === "Enemy")
                        this.sendNetworkMessage({
                            bulletToDestroy: bulletName,
                            damage: bulletToDelete.damage,
                        });

                    else if(collidedWith.includes("Shield"))
                        {
                            var handName= '';

                            if(collidedWith === 'rm_ShieldL')
                                handName= 'left';
                            else // if (collidedWith === 'rm_ShieldR')
                                handName= 'right';

                            this.sendNetworkMessage({
                                bulletToDestroy: bulletName,
                                shieldDamage: bulletToDelete.damage,
                                whichHand: handName, 
                            });
                        }

                    else 
                        this.sendNetworkMessage({
                        bulletToDestroy: bulletName,
                    });    
                
                    //The bullet has already been destroyed locally. 
                        //Because destroyBullet was called earlier.

                    //Lastly, delete the bullet.
                    bulletToDelete.destroyBullet();

                }

            //If the bullet is not owned by the Local Player,
                //then the Local Player must notify the bullet copy in its list.
            else
                {

                    //Actually, the remote Bullet is not checking collisions.
                        //the collider could be removed.
                    bulletToDelete.disableCollision();

                    //Delete the bullet.
                    bulletToDelete.destroyBullet();

                    console.log("-----------------------Remote Player: Deleted the Remote Bullet. firedBulletsList: ", this.firedBulletsList);

                }

            //console.log("---------->Local Player: "+this.firedBulletsList[bulletName].name + " deleted from list.");

            //Deleting bullet from the local list.
                //next, delete the bullet object itself.
                delete this.firedBulletsList[bulletName];
        }


//-----General Helper Function for Network Messaging.

sendNetworkMessage (message)
{

    if(this.isHost)
    {    
        if(this.connections.length)   
            {
                const clientConnection= this.connections[0];

                    clientConnection.send(message);
            }
    }

    //Else, if sending data from a Remote Client: connect to the this.connection variable.
    else if (this.connection)
    {
            this.connection.send(message);
    }    

}

