    setName_and_Owner(_name, owner) {

             this.object.name = _name;
             this.name= _name;
             this.Owner= owner;

        //Also setting the collision variables.
            this.collision= this.object.getComponent(CollisionComponent);

    }

    setAcceleration(newAcceleration){

        this.acceleration= newAcceleration;
        this.usesAcceleration=true;

    }

