    {
        console.log("Bullet name: "+ this.name +", speed: "+ this.speed +", acceleration: "+ this.acceleration+ ".");
                console.log("Forward World vector: "+ this.forwardWorld[0] + ", " + this.forwardWorld[1] +", "+ this.forwardWorld[2]+".");
    }

